<template>
  <div
    class="content-editor overflow-auto"
    style="
      min-height: 600px;
      max-height: 600px;
      scrollbar-width: thin;
      scrollbar-color: rgba(0, 0, 0, 0.2) transparent;
      right: -20px;
    "
  >
    <div class="section-group">
      <div class="border-b border-gray-200 pb-3 mb-3">
        <span class="popup-label block font-bold text-lg"
          >Lesson Categories Config</span
        >
        <span class="popup-label text-gray-500"
          >แสดงเนื้อหานี้ในหน้าจอของอุปกรณ์</span
        >
      </div>
    </div>

    <div class="section-group flex flex-col h-full pl-1">
      <!-- This assumes the inner 'lesson_categories_dropdown' component is globally registered or imported -->
      <lesson_categories_dropdown
        :selected-item="item"
        @update="handleChildUpdate"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'LessonCategoriesDropdownElement', // Distinct name for this wrapper
  props: {
    item: {
      type: Object,
      required: true,
    },
    mode: {
      type: String,
      required: true, // Expected 'edit' or 'preview'
    },
  },
  methods: {
    handleChildUpdate(updatePayload) {
      // This method adapts the child's update event to the expected 'update-item' event.
      // It assumes the child component might emit an object like { field, value }
      // or a partial item object. The goal is to emit the entire updated 'item'.
      const newItem = { ...this.item };

      if (updatePayload && typeof updatePayload.field === 'string' && 'value' in updatePayload) {
        // Handles { field, value } payload
        newItem[updatePayload.field] = updatePayload.value;
      } else if (updatePayload && typeof updatePayload === 'object') {
        // Handles a partial object payload to be merged
        Object.assign(newItem, updatePayload);
      }
      // If updatePayload is neither, the original item is emitted (or consider error/logging)

      this.$emit('update-item', newItem);
    },
  },
  // If the inner <lesson_categories_dropdown ... /> component is not globally registered,
  // you will need to import it here and register it in a 'components' section.
  // For example:
  // import ActualLessonCategoriesDropdown from './path/to/actual/lesson_categories_dropdown.vue';
  // components: {
  //   'lesson_categories_dropdown': ActualLessonCategoriesDropdown,
  // },
};
</script>

